import React from "react";
import StylizedH1 from "../../../shared/StylizedH1";

const HeroPedagogie = ({ h1, title, text, image }) => {
  return (
    <section className="hero">
      <StylizedH1 h1={h1} />
      <div className="hero-presentation">
        <div className="img-container">
          <img
            src={image}
            alt="illustration de la presentation de la pdagogie"
          />
        </div>
        <div className="hero-text">
          <h4>{title}</h4>
          <p>
            Lcole Lenvol des papillons est une cole alternative fonctionnant
            principalement avec la pdagogie de <span> Maria Montessori</span>,
            tout particulirement en maternelle. <br /> En lmentaire, nous
            utilisons galement dautres mthodes, qui nous permettent de
            correspondre au mieux aux diffrents profils des enfants.
            <br />
            <br />
            <span>
              Lcoute, lharmonie, la crativit et la confiance en soi
            </span>
            ladaptation aux besoins fondamentaux des enfants sont au cur de
            notre projet, tout cela dans le respect du socle commun de
            connaissances de lEducation Nationale.
          </p>
        </div>
      </div>
    </section>
  );
};

export default HeroPedagogie;
