import React from "react";
import Button from "../../shared/Button";
import PapillonLogo from "../../shared/PapillonLogo";

const EtablissementCollege = ({ title, text, color, link }) => {
  return (
    <article className="etablissement-card">
      <PapillonLogo color="blue" />
      <h4>{title}</h4>
      <p>
        Le collge alternatif Lenvol, ouvert depuis 2020, accueille vos enfants
        de la sixime  la troisime. Les classes comptent au maximum une
        douzaine d'lves, permettant de les accompagner au mieux en fonction de
        leurs besoins.
      </p>
      <Button color={color} text="Je dcouvre" link={link} />
    </article>
  );
};

export default EtablissementCollege;
