import React from "react";
import NavigationGlobal from "../../components/shared/NavigationGlobal";
import AllAvis from "../../components/shared/AllAvis";
import FooterGlobal from "../../components/shared/FooterGlobal";
import { Helmet } from "react-helmet";

import "styles/etablissement/Accueil/_accueil.scss";
import HeroHome from "components/etablissement/Acceuil/HeroHome";
import Etablissements from "components/etablissement/Acceuil/Etablissements";
import Locaux from "components/etablissement/Acceuil/Locaux";
const Accueil = () => {
  return (
    <>
      <Helmet>
        <title>Accueil - L'envol des papillons</title>
        <meta
          name="description"
          content="Dcouvrez L'Envol des Papillons, un tablissement accueillant une cole Montessori ainsi qu'un collge alternatif, fonde  Bordeaux en 2012, nous offrons une ducation bienveillante et individualise  un tarif abordable. veillez la passion d'apprendre de votre enfant ds aujourd'hui."
        />
        <meta
          property="og:image"
          content="../../../public/images/Accueil.png"
          content={`${process.env.PUBLIC_URL}/images/Accueil.png`}
        />
      </Helmet>
      <NavigationGlobal />
      <main>
        <HeroHome />
        <Etablissements />
        <AllAvis />

        <Locaux />
      </main>
      <FooterGlobal />
    </>
  );
};

export default Accueil;
