import React from "react";
import Button from "../../shared/Button";
import PapillonLogo from "../../shared/PapillonLogo";

const EtablissementSchool = ({ title, text, color, link }) => {
  return (
    <article className="etablissement-card">
      <PapillonLogo color="orange" />
      <h4>{title}</h4>
      <p>
        Lcole Lenvol des papillons, prsente depuis plus de 10 ans  Bordeaux
        dans le quartier des Chartrons, accueille vos enfants de 3  12 ans.{" "}
        <br /> Lensemble de lquipe, forme  la pdagogie Montessori,
        accompagne vos enfants avec bienveillance et apporte le cadre ncessaire
         leur panouissement de vos enfants.
      </p>
      <Button color={color} text="Je dcouvre" link={link} />
    </article>
  );
};

export default EtablissementSchool;
