import React from "react";
import StylizedH1 from "../../../shared/StylizedH1";

const HeroPedagogie = ({ h1, title, text, image }) => {
  return (
    <section className="hero">
      <StylizedH1 h1={h1} />
      <div className="hero-presentation">
        <div className="img-container">
          <img
            src={image}
            alt="illustration de la presentation de la pdagogie"
          />
        </div>
        <div className="hero-text">
          <h4>{title}</h4>
          <p>
            Le collge souhaite rester fidle aux valeurs de lcole dorigine :
            <span> coute, harmonie, crativit et confiance en soi</span>.
            Ladaptation aux besoins fondamentaux des enfants et des adolescents
            est au cur du projet du collge, tout cela dans le{" "}
            <span>
              {" "}
              respect du socle commun de connaissances de lEducation Nationale
            </span>
            .
          </p>
        </div>
      </div>
    </section>
  );
};

export default HeroPedagogie;
