import React from "react";
import EtablissementSchool from "./EtablissementSchool";
import EtablissementCollege from "./EtablissementCollege";

const Etablissements = () => {
  return (
    <section className="etablissements">
      <h3>Nos tablissements</h3>
      <article className="etablissements-container">
        <EtablissementSchool
          title="cole"
          color="orange"
          link="/ecole/presentation"
        />
        <EtablissementCollege
          title="Collge"
          text="Lcole Lenvol des papillons, prsente depuis plus de 10 ans  Bordeaux dans le quartier des Chartrons, accueille vos enfants de 3  12 ans. Lensemble de lquipe, forme  la pdagogie Montessori, accompagne vos enfants avec bienveillance et apporte le cadre ncessaire  lpanouissement de vos enfants.
"
          color="blue"
          link="/college/presentation"
        />
      </article>
    </section>
  );
};

export default Etablissements;
