import React from "react";
import SingleCaroussel from "../../../shared/SingleCaroussel";

const Materials = ({ text, images }) => {
  return (
    <section className="materials">
      <h3>Un matriel adapt</h3>
      <div className="materials-container">
        <p>
          Le matriel est propice au dveloppement et  lapprentissage de
          lenfant par lui-mme. Il repose sur des{" "}
          <span>valeurs fondamentales</span> : <br />
          <br />  il isole la difficult <br />
          <br />  il est sensoriel <br />
          <br /> il est adapt  lenfant <br />
          <br />  il est autocorrectif <br />
          <br /> il est harmonieux <br />
          <br />
          On trouve dans les classes{" "}
          <span>diffrentes catgories de matriel</span> : vie pratique, vie
          sensorielle, langage, ou franais, mathmatiques, culture (gographie,
          musique, botanique et zoologie)
        </p>
        <div className="caroussel-container">
          <SingleCaroussel images={images} />
        </div>
      </div>
    </section>
  );
};

export default Materials;
