import React from "react";

export default function Periscolaire() {
  return (
    <section className="periscolaire" id="periscolaire">
      <h3>Priscolaire (Mercredi)</h3>
      <p>
        Lcole propose aux enfants de lcole un temps daccueil ludique le
        mercredi hors vacances scolaires, de 8h30  17h30. <br />
        <br />
        Prix : 35 la journe, goter compris <br />
        <br />
        Possibilit de demi-journes pour les 3-6 ans (20 la demi-journe),
        jusqu 12h45 maximum.
      </p>
      <div className="periscolaire-container">
        <div className="periscolaire-schedule children">
          <h5>Droul de la journe : </h5>
          <ul>
            <li>- 8h30 - 9h30 : Accueil libre</li>
            <li>- 9h30  12h00 : activits</li>
            <li>- 12h00  14h00 : temps du midi (repas et temps calme)</li>
            <li>- 14h00  16h30 : activits et goter</li>
            <li>- 16h30  17h30 : dpart</li>
          </ul>
        </div>
        <div className="persicolaire-activity children">
          <h5>Activits proposes : </h5>
          <ul>
            <li>- Activits artistiques</li>
            <li>- Grands jeux ou activits sportives (6-11 ans)</li>
            <li>
              - Cuisine (prparation du goter en alternance par les 3-6 ans et
              les 6-11 ans)
            </li>
            <li>- Sorties, visites, cinma</li>
            <li>- Autres</li>
          </ul>
        </div>
        <div className="periscolaire-lunch-break children">
          <h5>Temps du midi (12h00 - 14h00) : </h5>
          <p>
            Le repas est apport par les familles (peut-tre possibilit de
            traiteur ultrieurement), suivi dun moment de repos ou de sieste
            pour ceux qui le souhaitent.
          </p>
        </div>
      </div>
    </section>
  );
}
