import React from "react";
import NavigationAdmin from "../../components/admin/NavigationAdmin";
import H1HeroText from "../../components/shared/H1HeroText";
import Actualities from "../../components/parents/Actualits/Actualities";
import { Helmet } from "react-helmet";

const Actualites = () => {
  return (
    <>
      <Helmet>
        <title>Actualits - ADMIN</title>
        <meta name="description" content="Description de Ma Page" />
      </Helmet>
      <NavigationAdmin />
      <main>
        <H1HeroText
          h1="Actualits"
          text="Grez l'ensemble des articles d'actualits de l'tablissement. Ajoutez, modifiez ou supprimez des articles d'actualit."
        />
        <Actualities />
      </main>
    </>
  );
};

export default Actualites;
